/**
 * promise
 */
import axios from 'axios'
import qs from 'qs'
import { BASE_URL_PAYHOME } from '../api'

export default function fetch (url = '', data = {}, method = 'get') {
  method = method.toLowerCase()
  let ops = {}
  if (method === 'post') {
    ops = { data: qs.stringify(data) }
    if (url.indexOf('/omm/ml.htm')) {
      ops = { data: data }
    }
  }

  // (...)
  let options = {
    url,
    ...ops,
    method
  }

  return new Promise((resolve, reject) => {
    // axios
    const instance = axios.create({
      // 
      baseURL: BASE_URL_PAYHOME,
      // 
      timeout: 30000,
      // cookie
      withCredentials: true,
      crossDomain: true
      // headers: {
      //   'Content-Type': 'application/json;charset=UTF-8',
      //   'X-Requested-With': 'XMLHttpRequest'
      // }
    })
    // 
    instance(options)
      .then(({ data }) => {
        return resolve(data)
      })
      .catch(error => {
        // ,
        if (error.response) {
          // let resError = error.response
          let resCode = error.status
          let resMsg = error.message
          let err = { code: resCode, respMsg: resMsg }
          // console.log('')
          return reject(err)
        } else {
          // console.log('')
          let err = { type: 'canceled', respMsg: '' }
          return reject(err)
        }
      })
  })
}