export function getLngLat () {
  let timeout = 4000
  let complate = false
  let u = window.navigator.userAgent
  let isIos = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
  let isJDJR = /jdjr-app/i.test(u.toLowerCase())
  return new Promise(function (resolve, reject) {
    let timer = window.setTimeout(function () {
      if (!complate) {
        complate = true
        resolve(null)
      }
    }, timeout)
    console.time('getGeoH5')
    window.navigator.geolocation.getCurrentPosition(
      function (pos) {
        if (!complate) {
          complate = true
          resolve({
            lat: pos.coords.latitude,
            lng: pos.coords.longitude
          })
          console.log('H5')
          console.timeEnd('getGeoH5')
        }
      }, function (err) {
        if (!complate) {
          if (!isJDJR) {
            resolve(null)
          }
          console.log('H5')
          console.timeEnd('getGeoH5')
        }
      }, {
        enableHighAccuracy: true,
        timeout: 4000,
        maximumAge: 0
      }
    )
    // 
    if (isJDJR) {
      console.time('timerJr')
      if (!isIos) {
        window.jdjrBridge = window.jsBridgeV3.onReady()
      }
      window.jdjrBridge.then(function (result) { // eslint-disable-line
        console.log('result:', result)
        result.jsToGetResp(function (d) {
          d = (typeof d === 'object') ? d : JSON.parse(d)
          console.log(d)
          if (!complate) {
            complate = true
            resolve({
              lat: d.location.latitude,
              lng: d.location.longitude
            })
            console.log('JR')
            console.timeEnd("timerJr")
          }
        }, {
            type: 9,
            isLocation: true,
            data: ''
          })
      })
    }
  })
}

function getH5LngLat (timeout) {
  return new Promise(function (resolve, reject) {
    let complate = false
    let timer = window.setTimeout(function () {
      if (!complate) {
        resolve(null)
      }
    }, timeout)
    // h5
    console.time('getGeoH5')
    window.navigator.geolocation.getCurrentPosition(
      function (pos) {
        complate = true
        resolve({
          lat: pos.coords.latitude,
          lng: pos.coords.longitude
        })
        console.log('H5')
        console.timeEnd('getGeoH5')
      }, function (err) {
        complate = true
        resolve(null)
        console.log('H5')
      }, {
        enableHighAccuracy: true,
        timeout: 100000,
        maximumAge: 0
      }
    )

  })
}

